/* Ensure layout matches MyTasks */
.dashboard-container {
  display: flex;
  flex-direction: column;
  height: 100vh;
}

/* ðŸ”¹ Top Bar */
.topbar {
  background: white;
  padding: 15px 20px;
  display: flex;
  align-items: center;
  box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.1);
}

.taskly-logo {
  height: 90px;
}

/* ðŸ”¹ Main Layout */
.content {
  display: flex;
  height: 100%;
}

.task-container {
  flex: 1;
  padding: 20px;
  background: #EFE6DC; /* Light beige background */
}

/* ðŸ”¹ Vital Task Box (Fix Missing Styling) */
.vital-task-box {
  background: white;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.1);
  max-width: 600px;
  margin: 0 auto;
}

/* ðŸ”¹ Task Cards */
.task-card {
  background: #f7f7f7;
  padding: 15px;
  margin: 10px 0;
  border-radius: 8px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.urgent {
  border-left: 4px solid red;
}
